/* ----  React Loader  ---- */
.loader-overlay,
.loader-overlay-local {
  position: relative; }
  .loader-overlay:before,
  .loader-overlay-local:before {
    content: '';
    display: none;
    position: fixed;
    z-index: 1110;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparent; }
  .loader-overlay.load::after,
  .loader-overlay-local.load::after {
    content: '';
    display: block;
    position: fixed;
    z-index: 1101;
    width: 100%;
    height: 100%;
    min-width: 2rem;
    max-width: 10rem;
    min-height: 2rem;
    max-height: 10rem;
    top: 50%;
    left: 50%;
    background-image: url("../images/icons/stat/spinner.svg");
    background-position: 30% center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    animation-name: loader-spin, loader-fade-in;
    animation-duration: 1s, .3s;
    animation-iteration-count: infinite, 1;
    animation-timing-function: steps(8), ease-in;
