/* ----  rdtPicker  ---- */

.rdtPicker {    
    background-color: $dropdown-bg;
    border: $dropdown-border-width solid $dropdown-border-color;
    @include border-radius($dropdown-border-radius);
    @include box-shadow($dropdown-box-shadow);

    width: 14rem;
}

.rdtPicker td, .rdtPicker th {
    height: 1.875rem;
}

.rdtPicker td.rdtDay:hover, .rdtPicker td.rdtHour:hover, .rdtPicker td.rdtMinute:hover, .rdtPicker td.rdtSecond:hover, .rdtPicker .rdtTimeToggle:hover,
.rdtPicker td.rdtMonth:hover, .rdtPicker td.rdtYear:hover
 {
    color: $dropdown-link-active-color; //$primary;
    background-color: $dropdown-link-hover-bg;
    border-radius: $dropdown-border-radius;
}

.rdtPicker td.rdtActive, .rdtPicker td.rdtActive:hover

{
    background-color: $dropdown-link-active-bg; //$primary;
    border-radius: $dropdown-border-radius;    
    color: $dropdown-link-active-color; //white;
    text-shadow: none;
}

.rdtPicker td.rdtToday::before {
	border-bottom-color: $dropdown-link-active-color; //$primary;
}

.rdtPicker td.rdtActive.rdtToday:before {
    border-bottom-color: $dropdown-link-active-color; //white;
}

	

.rdtPicker thead tr:first-child th:hover {
    color: $dropdown-link-active-color; //$primary;
    background-color: $dropdown-link-hover-bg;
    border-radius: $dropdown-border-radius;
}	

.rdtPicker th {
    border-bottom: none;
}

.rdtPicker td.rdtOld,
.rdtPicker td.rdtNew {
  color: $gray-500;
}

.rdtPicker th.rdtNext,
.rdtPicker th.rdtPrev
{
  width: 2rem;
}
  
.input-group > .rdt   // fix 
{ 
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  width: 1%;
  margin-bottom: 0;
}

